import React from "react"
import ReactApexChart from "react-apexcharts"

const Barchart = () => {
  const series = [
    {
      data: [380, 430, 450, 475],
    },
  ]
  const options = {
    chart: {
      toolbar: {
        show: false,
      },
    },
    plotOptions: {
      bar: {
        horizontal: false,
      },
    },
    dataLabels: {
      enabled: false,
    },

    colors: ["#34c38f"],
    grid: {
      borderColor: "#f1f1f1",
    },
    xaxis: {
      categories: [
        "Facebook",
        "Google",
        "Instagram",
        "Other"
      ],
    },
  }

  return (
    <ReactApexChart options={options} series={series} type="bar" height="350" />
  )
}

export default Barchart
