import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"
//import Lead from "./Lead";
import ClientLink from "./client-link";
import "flatpickr/dist/themes/material_blue.css"
import Select from "react-select"
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Label,
  Input,
  Button,
  InputGroup
} from "reactstrap"
import { AvField, AvForm, AvCheckbox } from "availity-reactstrap-validation"

const optionGroup = [
    {
      options: [
        { label: "Renting", value: "Renting" },
        { label: "Homeowner", value: "Homeowner" }
      ]
    }
  ]
  
  const sqrFts = [
    {
      options:[
        { label: "< 1000", value:"< 1000"},
        { label: "1000-1500", value:"1000-1500"},
        { label: "1501-2000", value:"1501-2000"},
        { label: "2001-2500", value:"2001-2500"},
        { label: "2501-3000", value:"2501-3000"},
        { label: "3001-3500", value:"3001-3500"},
      ]
    }
  ]
  
  const propIns = [
    {
      options:[
        { label: "Residential", value:"Residential"},
        { label: "Commercial", value:"Commercial"}
      ]
    }
  ]
  
  const propDeal = [
    {
      options:[
        { label: "1", value:"1"},
        { label: "2-3", value:"2-3"},
        { label: "3-4", value:"3-4"},
        { label: "4-5", value:"4-5"},
        { label: "5-7", value:"5-7"},
        { label: "7-10", value:"7-10"},
        { label: "+10", value:"+10"}
      ]
    }
  ]
  
  const propRisk = [
    {
      options:[
        { label: "Low Risk", value:"Low Risk"},
        { label: "Medium Risk", value:"Medium Risk"},
        { label: "High Risk", value:"High Risk"},
       
      ]
    }
  ]
class ClientInvPref extends Component {
  constructor(props) {
    super(props)
    this.state = {
        selectedGroup: null
    }
    this.handleSelectGroup = this.handleSelectGroup.bind(this)
  }
  handleSelectGroup = selectedGroup => {
    this.setState({ selectedGroup })
  }
  render() {
    const { selectedGroup } = this.state
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Client Appointment | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Clients")}
              breadcrumbItem={this.props.t("Investor Preference")}
            />
            <Row>
            
              
            <Col xl="12">
            <Card>
                                    <CardBody>
                                      <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Geographic locations preferred")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />  
                                        </FormGroup>
                                        </Col>
                                        </Row>
                                       

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Investment strategy")}
                                          </Label>
                                          <br />
                                          <div className="form-check form-check-inline">
                                          <input className="form-check-input" type="checkbox" id="inlineCheckbox1" value="option1" />
                                          <label className="form-check-label" htmlFor="inlineCheckbox1">Flip</label>
                                        </div>
                                        <div className="form-check form-check-inline">
                                          <input className="form-check-input" type="checkbox" id="inlineCheckbox2" value="option2" />
                                          <label className="form-check-label" htmlFor="inlineCheckbox2">Hold/Rent</label>
                                        </div> 
                                        <div className="form-check form-check-inline">
                                          <input className="form-check-input" type="checkbox" id="inlineCheckbox3" value="option3" />
                                          <label className="form-check-label" htmlFor="inlineCheckbox3">Sell</label>
                                        </div> 
                                        </FormGroup>
                                        
                                        </Col>
                                        
                                        </Row>

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Type of property interested")}
                                          </Label>
                                          <Select
                                              value=''
                                              onChange=''
                                              options={propIns}
                                              classNamePrefix="select2-selection"
                                            />
                                        </FormGroup>
                                        
                                        </Col>
                                        
                                        </Row>

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Price range")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />  
                                        </FormGroup>
                                        
                                        </Col>
                                        
                                        </Row>

                                       

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("How many deals do you typically have going at one time? ")}
                                          </Label>
                                          <Select
                                              value=''
                                              onChange=''
                                              options={propDeal}
                                              classNamePrefix="select2-selection"
                                            />
                                        </FormGroup>
                                        
                                        </Col>
                                        
                                        </Row>
                                        <Row className="mb15">
                                        <Col lg="6">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Expected profit / deal")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />  
                                        </FormGroup>
                                        
                                        </Col>
                                        <Col lg="6">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Risk capacity ")}
                                          </Label>
                                          <Select
                                              value=''
                                              onChange=''
                                              options={propRisk}
                                              classNamePrefix="select2-selection"
                                            />
                                        </FormGroup>
                                        
                                        </Col>
                                        </Row>
                                        

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Investor notes")}
                                          </Label>
                                          <textarea
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          ></textarea>
                                        </FormGroup>
                                        </Col>
                                        </Row>

                                      

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <Button
                                            color="primary"
                                          >
                                            Save Questions
                                          </Button>
                                        </Col>
                                        </Row>
                                    </CardBody>
                                </Card>
            </Col>
            
            </Row>
           
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
ClientInvPref.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(ClientInvPref))
