import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"
import LetestContact from "./LetestContact";
import UpcomingFollow from "./Upcoming-follow"
import LeadFeedback from "./LeedFeedback"
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle
} from "reactstrap"

class Dashboard extends Component {
  constructor(props) {
    super(props)
    this.state = {
      reports: [
        { title: "Investor", iconClass: "bx-copy-alt", description: "14" },
        {
          title: "Platinum",
          iconClass: "bx-archive-in",
          description: "6",
        },
        {
          title: "Gold",
          iconClass: "bx-purchase-tag-alt",
          description: "5",
        },
        {
          title: "Silver",
          iconClass: "bx-purchase-tag-alt",
          description: "2",
        },
      ]
    }
  }
  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Contact Dashboard | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Contact")}
              breadcrumbItem={this.props.t("Contact Dashboard")}
            />
            <Row className="mb20">
                <Col xl="12">
                <Link
                      to="/contact/contact-create"
                      className="btn btn-danger btn-md float-end"
                    >
                      Add Contact {" "}<i className="mdi mdi-plus-box-outline ms-1"/>
                    </Link> 

             
              </Col>

            </Row>
            <Row>
              
              <Col xl="12">
              
                <Row>
            {this.state.reports.map((report, key) => (
            <Col md="3" key={"_col_" + key}>
                <Card className="mini-stats-wid">
                        <CardBody>
                          <div className="d-flex">
                            <div className="flex-grow-1">
                              <p className="text-muted fw-medium">
                                {report.title}
                              </p>
                              <h4 className="mb-0">{report.description}</h4>
                            </div>
                            <div className="mini-stat-icon avatar-sm rounded-circle bg-primary align-self-center">
                              <span className="avatar-title">
                                <i
                                  className={
                                    "bx " + report.iconClass + " font-size-24"
                                  }
                                />
                              </span>
                            </div>
                          </div>
                        </CardBody>
                      </Card>
                
              </Col>
            ))}
            </Row>
           
           
            </Col>
            </Row>
            <Row>
              <Col xl="4">
              <Card>
          <CardBody>
            <CardTitle className="mb-4 h4">Top referrers</CardTitle>
            

           
          </CardBody>
        </Card>
              </Col>
             
              <Col xl="4">
              <Card>
          <CardBody>
            <CardTitle className="mb-4 h4"> Leads by referral</CardTitle>
            

           
          </CardBody>
        </Card>
              </Col>
              
              <Col xl="4">
              <Card>
          <CardBody>
            <CardTitle className="mb-4 h4">Clients by referral</CardTitle>
            

           
          </CardBody>
        </Card>
              </Col>
            </Row>
            <Row>
            <Col lg="12">
             <LetestContact />
            </Col>
          </Row>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
Dashboard.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(Dashboard))
