import ClientInvPref from "../pages/Clients/client-inv-pref"

const authProtectedRoutes = [
 { path: "/dashboard", component: DashboardNew },

  //profile
  { path: "/Pre-Construction/Project", component: Project },
  { path: "/Pre-Construction/create-project", component: ProjectsCreate },
  { path: "/Pre-Construction/project-detail", component: ProjectDetail },
  { path: "/Pre-Construction/Campaign", component: Campaign },

  { path: "/Pre-Construction/Builder", component: Builder },
  { path: "/Pre-Construction/Builder/Builder-detail", component: BuilderDetail },
  { path: "/Pre-Construction/Builder/create", component: BuilderCreate },


  { path: "/activity", component: Activity },
  { path: "/activity/calander", component: Calender },

  { path: "/lead/dashboard", component: Dashboard },
  { path: "/lead/leadlist", component: LeadList },
  { path: "/lead/lead-detail", component: LeadDetail },
  { path: "/lead/lead-create", component: CreateLead },

  //Contatcs

  { path: "/Contacts", component: Contacts },
  { path: "/all-contacts", component: AllContacts },
  { path: "/contact/contact-detail", component: ContactDetail },
  { path: "/contact/contact-report", component: ContactReport },
  { path: "/contact/contact-create", component: ContactCreate },
  { path: "/contact/contact-edit", component: EditContact },
  { path: "/contact/classification-view", component: ClassiView },
   { path: "/contact/activity", component: ClientActivity },
    { path: "/contact/in-activity", component: ClientinActivity },
  // Email Composer

  { path: "/email", component: EmailComposer },
  { path: "/email-2", component: EmailSec },
  { path: "/new-email", component: NewEmail },

  //CLients
 
  { path: "/clients", component: Clients },
  { path: "/clients/dashbord", component: ClientDashbord },
  { path: "/clients/profile", component: ClientProfile },
  { path: "/clients/message", component: ClientMessage },
  { path: "/clients/documents", component: ClientDocument },
  { path: "/clients/appointment", component: ClientApp },
  { path: "/clients/notes", component: ClientNote },
  { path: "/clients/sale", component: ClientSale },
  { path: "/clients/sale-preference", component: ClientSalePref },
  { path: "/clients/seller-showings", component: ClientSellerShow },
  { path: "/clients/seller-closing", component: ClientSellerClose },
  { path: "/clients/seller-open", component: ClientSellerOpen },
  { path: "/clients/seller-check", component: ClientSellerCheck },
  { path: "/clients/seller-market", component: ClientSellerMarket },

  
  { path: "/clients/buyer-preference", component: ClientBuyPref },
   { path: "/clients/buyer-closing", component: ClientBuyerClose },
   { path: "/clients/buyer-market", component: ClientBuyerMarket },
    { path: "/clients/buyer-check", component: ClientBuyerCheck },

    { path: "/clients/investor-preference", component: ClientInvPref },
  //ClientDashbord
  // this route should be at the end of all other routes
  // eslint-disable-next-line react/display-name
  { path: "/", exact: true, component: () => <Redirect to="/dashboard" /> }
]

const publicRoutes = [
  
  { path: "/logout", component: Logout },
  { path: "/login", component: Login },
  { path: "/forgot-password", component: ForgetPwd }
]

export { authProtectedRoutes, publicRoutes }
