import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"
//import Lead from "./Lead";
import WelcomeComp from "./WelcomeComp"
import UpcomingFollow from "./Upcoming-follow"
import LeadBySource from "./lead-chart"
import LeadByLoss from "./lead-lost"
import Barchart from "./barchart"

import Revnue from "../AllCharts/apex/revnueLine"
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle
} from "reactstrap"
import PieChart from "../AllCharts/chartjs/piechart"
import {
  Sparklines,
  SparklinesLine,
  SparklinesSpots,
} from "react-sparklines"
import LatestLeads from "./LatestLeads"
class Dashboard extends Component {
  constructor(props) {
    super(props)
    this.state = {
      reports: [
        { title: "Won vs lost %", iconClass: "bx-copy-alt", description: "5" },
       
        {
          title: "Hot leads",
          iconClass: "bx-purchase-tag-alt",
          description: "20",
        },
        {
          title: "Total Leads",
          iconClass: "bx-purchase-tag-alt",
          description: "20",
        },
        {
          title: "Pipeline value",
          iconClass: "bx-purchase-tag-alt",
          description: "20",
        },
      ]
    }
  }
  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Lead Dashboard | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Lead")}
              breadcrumbItem={this.props.t("Lead Dashboard")}
            />
            <Row>
            <Col xl="4">
               <WelcomeComp />
              
                
                
            
               
              
              </Col>
              <Col xl="12">
              
                <Row>
            {this.state.reports.map((report, key) => (
            <Col md="3" key={"_col_" + key}>
                <Card className="mini-stats-wid">
                        <CardBody>
                          <div className="d-flex">
                            <div className="flex-grow-1">
                              <p className="text-muted fw-medium">
                                {report.title}
                              </p>
                              <h4 className="mb-0">{report.description}</h4>
                            </div>
                            <div className="mini-stat-icon avatar-sm rounded-circle bg-primary align-self-center">
                              <span className="avatar-title">
                                <i
                                  className={
                                    "bx " + report.iconClass + " font-size-24"
                                  }
                                />
                              </span>
                            </div>
                          </div>
                        </CardBody>
                      </Card>
                
              </Col>
            ))}
            </Row>
            <Row>
            <Col lg={4}>
              <Card>
                <CardBody>
                  <CardTitle className="mb-4 float-start">Leads by type</CardTitle>
                  <div className="float-end">
                       <select name="" id="" className="form-control">
                         <option value="">Select Days</option>
                         <option value="">30 Days</option>
                         <option value="">60 Days</option>
                         <option value="">90 Days</option>
                         <option value="">120 Days</option>
                      </select>             

                </div>
                <div className="clearfix"></div>
                  

                  <LeadBySource />
                </CardBody>
              </Card>
            </Col>
            <Col lg="4">
            <Card>
                <CardBody>
                  <CardTitle className="mb-4 float-start">Top sources for lead generation</CardTitle>
                  <div className="float-end">
                       <select name="" id="" className="form-control">
                         <option value="">Select Days</option>
                         <option value="">30 Days</option>
                         <option value="">60 Days</option>
                         <option value="">90 Days</option>
                         <option value="">120 Days</option>
                      </select>             

                </div>
                <div className="clearfix"></div>
                  <Barchart />
                </CardBody>
              </Card>                    

            </Col>
            <Col lg={4}>
              <Card>
                <CardBody>
                  <CardTitle className="mb-4 float-start">Loss report </CardTitle>
                  <div className="float-end">
                       <select name="" id="" className="form-control">
                         <option value="">Select Days</option>
                         <option value="">30 Days</option>
                         <option value="">60 Days</option>
                         <option value="">90 Days</option>
                         <option value="">120 Days</option>
                      </select>             

                </div>
                <div className="clearfix"></div>
                  

                  <LeadByLoss />
                </CardBody>
              </Card>
            </Col>
            </Row>
            
            </Col>
            </Row>
            <Row>
            <Col lg="12">
              <LatestLeads />
            </Col>
          </Row>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
Dashboard.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(Dashboard))
