import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"
//import Lead from "./Lead";
import ClientLink from "./client-link";
import "flatpickr/dist/themes/material_blue.css"
import Flatpickr from "react-flatpickr"
import Calender from "../Activities/calander"
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Label,
  Input,
  Button,
  InputGroup
} from "reactstrap"
import { AvField, AvForm, AvCheckbox } from "availity-reactstrap-validation"
class ClientSalePref extends Component {
  constructor(props) {
    super(props)
    this.state = {
    }
  }
  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Client Appointment | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Clients")}
              breadcrumbItem={this.props.t("Seller Preference")}
            />
            <Row>
            
              
            <Col xl="12">
            <Card>
                                    <CardBody>
                                      <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Reason to sell")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />  
                                        </FormGroup>
                                        </Col>
                                        </Row>
                                       

                                        <Row className="mb15">
                                        <Col lg="9">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Have you listed the property previously")}
                                          </Label>
                                            
                                        </FormGroup>
                                        
                                        </Col>
                                        <Col lg="3">
                                        <div className="square-switch">
                                          <input
                                            type="checkbox"
                                            id="square-switch1"
                                            switch="none"
                                            checked={this.state.sq1}
                                            onChange={() =>
                                              this.setState({ sq1: !this.state.sq1 })
                                            }
                                          />
                                          <label
                                            htmlFor="square-switch1"
                                            data-on-label="Yes"
                                            data-off-label="No"
                                          />
                                        </div>
                                        </Col>
                                        </Row>

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Expected price")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />  
                                        </FormGroup>
                                        
                                        </Col>
                                        
                                        </Row>

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("How soon do you want to move out")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />  
                                        </FormGroup>
                                        
                                        </Col>
                                        
                                        </Row>

                                       

                                        <Row className="mb15">
                                        <Col lg="9">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Are you looking to buy a new property after selling ")}
                                          </Label>
                                            
                                        </FormGroup>
                                        
                                        </Col>
                                        <Col lg="3">
                                        <div className="square-switch">
                                          <input
                                            type="checkbox"
                                            id="square-switch1"
                                            switch="none"
                                            checked={this.state.sq1}
                                            onChange={() =>
                                              this.setState({ sq1: !this.state.sq1 })
                                            }
                                          />
                                          <label
                                            htmlFor="square-switch1"
                                            data-on-label="Yes"
                                            data-off-label="No"
                                          />
                                        </div>
                                        </Col>
                                        </Row>
                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Who else is going to be helping you in making the decision?")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />  
                                        </FormGroup>
                                        
                                        </Col>
                                        
                                        </Row>
                                        

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Tell a little bit about your home/neighbourhood")}
                                          </Label>
                                          <textarea
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          ></textarea>
                                        </FormGroup>
                                        </Col>
                                        </Row>

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Seller notes")}
                                          </Label>
                                          <textarea
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          ></textarea>
                                        </FormGroup>
                                        </Col>
                                        </Row>

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <Button
                                            color="primary"
                                          >
                                            Save Questions
                                          </Button>
                                        </Col>
                                        </Row>
                                    </CardBody>
                                </Card>
            </Col>
            
            </Row>
           
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
ClientSalePref.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(ClientSalePref))
