import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"
//import Lead from "./Lead";
import ClientLink from "./client-link";
import "flatpickr/dist/themes/material_blue.css"
import Select from "react-select"
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Label,
  Input,
  Button,
  InputGroup
} from "reactstrap"
import { AvField, AvForm, AvCheckbox } from "availity-reactstrap-validation"

const optionGroup = [
    {
      options: [
        { label: "Renting", value: "Renting" },
        { label: "Homeowner", value: "Homeowner" }
      ]
    }
  ]
  
  const sqrFts = [
    {
      options:[
        { label: "< 1000", value:"< 1000"},
        { label: "1000-1500", value:"1000-1500"},
        { label: "1501-2000", value:"1501-2000"},
        { label: "2001-2500", value:"2001-2500"},
        { label: "2501-3000", value:"2501-3000"},
        { label: "3001-3500", value:"3001-3500"},
      ]
    }
  ]
  
  const propIns = [
    {
      options:[
        { label: "Residential", value:"Residential"},
        { label: "Commercial", value:"Commercial"}
      ]
    }
  ]
  
  const propDeal = [
    {
      options:[
        { label: "1", value:"1"},
        { label: "2-3", value:"2-3"},
        { label: "3-4", value:"3-4"},
        { label: "4-5", value:"4-5"},
        { label: "5-7", value:"5-7"},
        { label: "7-10", value:"7-10"},
        { label: "+10", value:"+10"}
      ]
    }
  ]
  
  const propRisk = [
    {
      options:[
        { label: "Low Risk", value:"Low Risk"},
        { label: "Medium Risk", value:"Medium Risk"},
        { label: "High Risk", value:"High Risk"},
       
      ]
    }
  ]
class ClientBuyPref extends Component {
  constructor(props) {
    super(props)
    this.state = {
        selectedGroup: null
    }
    this.handleSelectGroup = this.handleSelectGroup.bind(this)
  }
  handleSelectGroup = selectedGroup => {
    this.setState({ selectedGroup })
  }
  render() {
    const { selectedGroup } = this.state
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Client Appointment | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Clients")}
              breadcrumbItem={this.props.t("Buyer Preference")}
            />
            <Row>
            
              
            <Col xl="12">
            <Card>
                                    <CardBody>
                                      <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Are you currently renting or do you own a home")}
                                          </Label>
                                          <Select
                                              value={selectedGroup}
                                              onChange={this.handleSelectGroup}
                                              options={optionGroup}
                                              classNamePrefix="select2-selection"
                                            />
                                        </FormGroup>
                                        </Col>
                                        </Row>
                                       

                                        <Row className="mb15">
                                        <Col lg="9">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("If homeowner, then Do you need to sell before you buy?")}
                                          </Label>
                                            
                                        </FormGroup>
                                        
                                        </Col>
                                        <Col lg="3">
                                        <div className="square-switch">
                                          <input
                                            type="checkbox"
                                            id="square-switch1"
                                            switch="none"
                                            checked={this.state.sq1}
                                            onChange={() =>
                                              this.setState({ sq1: !this.state.sq1 })
                                            }
                                          />
                                          <label
                                            htmlFor="square-switch1"
                                            data-on-label="Yes"
                                            data-off-label="No"
                                          />
                                        </div>
                                        </Col>
                                        </Row>

                                        <Row className="mb15">
                                        <Col lg="9">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Have you signed a listing agreement to sell your home")}
                                          </Label>
                                            
                                        </FormGroup>
                                        
                                        </Col>
                                        <Col lg="3">
                                        <div className="square-switch">
                                          <input
                                            type="checkbox"
                                            id="square-switch1"
                                            switch="none"
                                            checked={this.state.sq1}
                                            onChange={() =>
                                              this.setState({ sq1: !this.state.sq1 })
                                            }
                                          />
                                          <label
                                            htmlFor="square-switch1"
                                            data-on-label="Yes"
                                            data-off-label="No"
                                          />
                                        </div>
                                        </Col>
                                        </Row>

                                        <Row className="mb15">
                                        <Col lg="9">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Are you pre-approved by a lender")}
                                          </Label>
                                            
                                        </FormGroup>
                                        
                                        </Col>
                                        <Col lg="3">
                                        <div className="square-switch">
                                          <input
                                            type="checkbox"
                                            id="square-switch1"
                                            switch="none"
                                            checked={this.state.sq1}
                                            onChange={() =>
                                              this.setState({ sq1: !this.state.sq1 })
                                            }
                                          />
                                          <label
                                            htmlFor="square-switch1"
                                            data-on-label="Yes"
                                            data-off-label="No"
                                          />
                                        </div>
                                        </Col>
                                        </Row>

                                        <Row className="mb15">
                                        <Col lg="3">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                          {this.props.t("How much will your down payment be ?")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />  
                                        </FormGroup>
                                        
                                        </Col>
                                        <Col lg="3">
                                        <FormGroup>
                                        <Label htmlFor="priceRange">
                                          {this.props.t(" What price range are you looking in ?")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />  
                                        </FormGroup>
                                        
                                        </Col>
                                        <Col lg="3">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                         {this.props.t("How many bedrooms")}
                                          </Label>
                                          <Select
                                              value=''
                                              onChange=''
                                              options=''
                                              classNamePrefix="select2-selection"
                                            />
                                        </FormGroup>
                                        </Col>
                                        <Col lg="3">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                           {this.props.t("How much Sq. ft")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />
                                        </FormGroup>
                                        </Col>
                                        </Row>

                                       

                                        

                                       

                                        <Row className="mb15">
                                        <Col lg="6">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("Preferred location")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />
                                        </FormGroup>
                                        </Col>
                                        <Col lg="6">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("By what date are you looking to move in")}
                                          </Label>
                                          <input
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          />
                                        </FormGroup>
                                        </Col>
                                        </Row>

                                       

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <FormGroup>
                                        <Label htmlFor="propertyKind">
                                           <i className="bx bx-play font-size-12 align-middle text-primary mr-1n"></i> {this.props.t("What else are you looking for in your home ?")}
                                          </Label>
                                          <textarea
                                            className="form-control"
                                            type="text"
                                            placeholder=""
                                          ></textarea>
                                        </FormGroup>
                                        </Col>
                                        </Row>

                                        <Row className="mb15">
                                        <Col lg="12">
                                        <Button
                                            color="primary"
                                          >
                                            Save Questions
                                          </Button>
                                        </Col>
                                        </Row>
                                    </CardBody>
                                </Card>
            </Col>
            
            </Row>
           
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
ClientBuyPref.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(ClientBuyPref))
