import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"
//import Lead from "./Lead";
import ClientLink from "./client-link";
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Label,
  Input,
  Button
} from "reactstrap"
import PieChart from "../AllCharts/chartjs/piechart"
import {
  Sparklines,
  SparklinesLine,
  SparklinesSpots,
} from "react-sparklines"
import { AvField, AvForm } from "availity-reactstrap-validation"
class ClientDocument extends Component {
  constructor(props) {
    super(props)
    this.state = {
    }
  }
  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Client Documents | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Clients")}
              breadcrumbItem={this.props.t("Mohit Grover Documents")}
            />
            <Row>
              <Col xl="3">
              <ClientLink />
              </Col>
              
              <Col xl="9">
              <AvForm className="needs-validation">
              <Card className="mini-stats-wid"> 
                    <CardBody>
                    <h5 className="card-title"></h5>
              
                   <Row>
                        <Col sm="6">
                        <FormGroup className="mb-3">
                            <Label htmlFor="minBedRoom">
                              {this.props.t("Document Title")}
                            </Label>
                            <AvField
                              name="minBedRoom"
                              value="Broucher"
                              
                              type="text"
                              errorMessage={this.props.t("Enter Min Bedroom")}
                              className="form-control"
                              validate={{ required: { value: false } }}
                              id="minBedRoom"
                            />
                          </FormGroup>
                        </Col>

                        <Col sm="6">
                        <FormGroup className="mb-3">
                            <Label htmlFor="minBedRoom">
                              {this.props.t("Action/Info")}
                            </Label>
                            <AvField
                              name="minBedRoom"
                              value="Information"
                              
                              type="text"
                              errorMessage={this.props.t("Enter Min Bedroom")}
                              className="form-control"
                              validate={{ required: { value: false } }}
                              id="minBedRoom"
                            />
                          </FormGroup>
                        </Col>
                    </Row>

                    <Row>
                        <Col sm="4">
                        <FormGroup className="mb-3">
                            <Label htmlFor="minBedRoom">
                              {this.props.t("Select File")}
                            </Label>
                            <input type="file" />
                          </FormGroup>
                        </Col>

                        <Col sm="4">
                        <FormGroup className="mb-3">
                            <Label htmlFor="minBedRoom">
                              {this.props.t("Type")}
                            </Label>
                            <AvField
                              name="minBedRoom"
                              value="General"
                              type="text"
                              className="form-control"
                              id="minBedRoom"
                            />
                          </FormGroup>
                        </Col>
                        <Col sm="4">
                        <FormGroup className="mb-3">
                            <Label htmlFor="minBedRoom">
                              {this.props.t("Is Urgent")}
                            </Label>
                            <AvField
                              name="minBedRoom"
                              value="No"
                              type="text"
                              className="form-control"
                              id="minBedRoom"
                            />
                          </FormGroup>
                        </Col>
                    </Row>
                    <div className="clearfix"><button type="submit" className="btn btn-primary w-md float-end">Upload Document</button></div>
             
             </CardBody>
                </Card>
                
             </AvForm>
			 
			 <Row>
				<Col sm="12">
					 <Card className="mini-stats-wid"> 
                    <CardBody>
                    <h5 className="card-title">Document History</h5>
					<div className="table-responsive">
						  <table className="table mb-0 table">
							<thead>
							  <tr>
								<th>#</th>
								<th>Title</th>
								<th>Action</th>
								<th>Type</th>
								<th>Is Urgent</th>
							  </tr>
							</thead>
							<tbody>
							  <tr>
								<th scope="row">1</th>
								<td>Broucher</td>
								<td>Information</td>
								<td>General</td>
								<td>No</td>
							  </tr>
							  
							</tbody>
						  </table>
						</div>
					</CardBody>
					</Card>
				</Col>
			</Row>
            </Col>
            
            </Row>
            
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
ClientDocument.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(ClientDocument))
